<template>
    <div class="vue-doc-wrapper">
        <div class="vue-doc-submenu">
          <ul class='vue-doc-submenu-list'><li><a href="#vue-doc-基础用法1">基础用法</a></li><li><a href="#vue-doc-精度1">精度</a></li><li><a href="#vue-doc-格式化数据1">格式化数据</a></li><li><a href="#vue-doc-尺寸1">尺寸</a></li><li><a href="#vue-doc-禁用1">禁用</a></li><li><a href="#vue-doc-只读1">只读</a></li><li><a href="#vue-doc-不可编辑1">不可编辑</a></li></ul><ul class='vue-doc-submenu-list-EN'><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li><li><a href="#vue-doc-ButtonSize1">Button Size</a></li></ul>
        </div>
        <div class="vue-doc-title">
          <h1 id="vue-doc-inputnumber数字输入框1" class="vue-doc-element">InputNumber 数字输入框</h1>
<p class="vue-doc-element">使用鼠标或键盘输入一定范围的标准数值。</p>

        </div>
        <div class="vue-doc-title-EN">
          <h1 id="vue-doc-button1" class="vue-doc-element">Button</h1>
<p class="vue-doc-element">Basic component that is used to trigger bussiness logic.</p>

        </div>

        <div class="vue-doc-components">
          <demobasic></demobasic>
<demostep></demostep>
<demoparse></demoparse>
<demosize></demosize>
<demodisabled></demodisabled>
<demoreadonly></demoreadonly>
<demoeditable></demoeditable>
        </div>


        <div class="vue-doc-content">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<h3 id="vue-doc-inputnumberprops1_1" class="vue-doc-element">InputNumber props</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>属性</th>
<th>说明</th>
<th>类型</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>max</td>
<td>最大值</td>
<td>Number</td>
<td>Infinity</td>
</tr>
<tr>
<td>min</td>
<td>最小值</td>
<td>Number</td>
<td>-Infinity</td>
</tr>
<tr>
<td>value</td>
<td>当前值，可以使用 v-model 双向绑定数据</td>
<td>Number</td>
<td>1</td>
</tr>
<tr>
<td>step</td>
<td>每次改变的步伐，可以是小数</td>
<td>Number</td>
<td>1</td>
</tr>
<tr>
<td>size</td>
<td>输入框尺寸，可选值为large、small、default或者不填</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>disabled</td>
<td>设置禁用状态</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>placeholder</td>
<td>占位文本</td>
<td>String</td>
<td>-</td>
</tr>
<tr>
<td>formatter</td>
<td>指定输入框展示值的格式</td>
<td>Function</td>
<td>-</td>
</tr>
<tr>
<td>parser</td>
<td>指定从 formatter 里转换回数字的方式，和 formatter 搭配使用</td>
<td>Function</td>
<td>-</td>
</tr>
<tr>
<td>readonly</td>
<td>是否设置为只读</td>
<td>Boolean</td>
<td>false</td>
</tr>
<tr>
<td>editable</td>
<td>是否可编辑</td>
<td>Boolean</td>
<td>true</td>
</tr>
<tr>
<td>precision</td>
<td>数值精度</td>
<td>Number</td>
<td>-</td>
</tr>
<tr>
<td>element-id</td>
<td>给表单元素设置 id，详见 Form 用法。</td>
<td>String</td>
<td>-</td>
</tr>
</tbody>
</table>
<h3 id="vue-doc-inputnumberevents1_2" class="vue-doc-element">InputNumber events</h3>
<table class="vue-doc-element">
<thead>
<tr>
<th>事件名</th>
<th>说明</th>
<th>返回值</th>
</tr>
</thead>
<tbody>
<tr>
<td>on-change</td>
<td>数值改变时的回调，返回当前值</td>
<td>当前值</td>
</tr>
<tr>
<td>on-focus</td>
<td>聚焦时触发</td>
<td>event</td>
</tr>
<tr>
<td>on-blur</td>
<td>失焦时触发</td>
<td>-</td>
</tr>
</tbody>
</table>

        </div>
        <div class="vue-doc-content-EN">
          <h2 id="vue-doc-api1" class="vue-doc-element">API</h2>
<table class="vue-doc-element">
<thead>
<tr>
<th>Attribute</th>
<th style="text-align:right">Explanation</th>
<th>Type</th>
<th>Default</th>
</tr>
</thead>
<tbody>
<tr>
<td>type</td>
<td style="text-align:right">Button type, options include primary、ghost、dashed、text、info、success、warning、error, optional</td>
<td>String</td>
<td>-</td>
</tr>
</tbody>
</table>

        </div>
        


    </div>
  </template>
  <script>
      
  import demobasic from './demo/basic.md';
import demostep from './demo/step.md';
import demoparse from './demo/parse.md';
import demosize from './demo/size.md';
import demodisabled from './demo/disabled.md';
import demoreadonly from './demo/readonly.md';
import demoeditable from './demo/editable.md';
  import ClipboardJS from "clipboard";
  const mixin = {
    data(){
      return{
        showVueDocCode:false,
        vueDocCodeCopyText:"copy",
        vueDocCodeHeight:0
      }
    },
    methods: {
      vueDocCodeToggle() {
        this.showVueDocCode=!this.showVueDocCode;
        this.vueDocCodeHeight=this.vueDocCodeHeight||this.$refs.vueDocCodeBox.children[0].offsetHeight;
        this.$refs.vueDocCodeBox.style.height=this.showVueDocCode?(this.vueDocCodeHeight+50)+'px':'0px';
      },
      vueDocCodeCopy(){
        const clipboard = new ClipboardJS('.vue-doc-code-copy',{
          text:(trigger)=> {
                return trigger.nextElementSibling.textContent;
            }
        });
        clipboard.on('success', (e)=> {
            this.vueDocCodeCopyText="copy success!";
            setTimeout(()=>{
              this.vueDocCodeCopyText="copy";
            },1000);
            e.clearSelection();
            clipboard.destroy();
        });
      }
    },
    components:{
      demobasic,demostep,demoparse,demosize,demodisabled,demoreadonly,demoeditable
    }
  };
  
      export default {
        mixins:[mixin]
      }
      </script>
  
  <style lang="less">
    @import "ivue-doc-loader/styles/code/default.css";
    @import "ivue-doc-loader/styles/default.css";
    .vue-doc-code-content{
      height:0;
      overflow:hidden;
      transition: height .3s;
      background:#f8f8f8;
    }
    .vue-doc-code-show .vue-doc-code-content{
      height:auto;
    }
    .vue-doc-content-EN,.vue-doc-title-EN,.vue-doc-submenu-list-EN,.vue-doc-code-toggle-text-EN{
      display:none;
    }
    .en-US .vue-doc-submenu-list-EN{
      display:block;
    }
    .en-US .vue-doc-submenu-list{
      display:none;
    }

    .en-US .vue-doc-content-EN{
      display:block;
    }
    .en-US .vue-doc-content{
      display:none;
    }

    .en-US .vue-doc-title-EN{
      display:block;
    }
    .en-US .vue-doc-title{
      display:none;
    }

    .en-US .vue-doc-code-toggle-text-EN{
      display:block;
    }
    .en-US .vue-doc-code-toggle-text{
      display:none;
    }

    .vue-doc-demo{
      padding: 20px;
      border:1px solid #ddd;
      border-bottom: 0;
      border-top-left-radius: 5px;
      border-top-right-radius: 5px;
    }
    .vue-doc-code{
      border: 1px solid #ddd;
      position: relative;
      padding-bottom: 50px;
      border-bottom-left-radius: 5px;
      border-bottom-right-radius: 5px;
    }
    .vue-doc-code-copy{
      display: none;
      position: absolute;
      right: 15px;
      font-weight: bold;
      top: 10px;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-copy{
      display: block;
    }
    .vue-doc-code-toggle{
      position: absolute;
      bottom: 0;
      left: 0;
      width:100%;
      font-weight: bold;
      height: 50px;
      line-height: 50px;
      text-align: center;
      cursor: pointer;
    }
    .vue-doc-code-show .vue-doc-code-toggle{
      border-top: 1px solid #ddd;
    }

    .vue-doc-code .vue-doc-code-content pre{
      border: 0;
      padding: 15px;
    }

    .vue-doc-wrapper{
      margin-bottom:50px;
    }



  </style>


